import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;

import Vue from 'vue'
import App from './App'
import uView from '@uni/uview-ui';
import store from './store/index'
import Plugins from '@/utils/plugins.js'
Vue.use(Plugins)
Vue.use(uView);
//阻止 vue 在启动时生成生产提示
if (process.env.NODE_ENV === 'development') {
  Vue.config.productionTip = false
}
App.mpType = 'app'
const app = new Vue({
  ...App,
  store
})
app.$mount()